<template>
  <block title="">
      <div class="chart-box" ref="chartRef">
      </div>
    </block>
</template>

<script>
import block from '../../../../component/block'
import echarts from 'echarts'
const dataIPSxAxis = ['04', '08', '12', '16', '20', '24']
const dataIPS = []
const dataIPS2 = []
const dataIPS3 = []

const options = {

  tooltip: {
    trigger: 'axis',
    axisPointer: {
      type: 'cross',
      label: {
        backgroundColor: '#6a7985'
      }
    }
  },
  legend: {
    data: ['未戴白色厨帽', '未戴白色口罩', '未穿白色厨师服'],
    textStyle: {
      color: '#e9ebee'
    },
    right: '5%'

  },
  color: ['#0080ff', '#4cd5ce', '#bf4696'],
  toolbox: {
    // feature: {
    //     saveAsImage: {}
    // }
  },
  grid: {
    x: 46,
    y: 30,
    x2: 30,
    y2: 20,
    borderWidth: 0
  },

  xAxis: [{
    type: 'category',
    boundaryGap: false,
    data: dataIPSxAxis,
    axisLabel: {
      show: true,
      textStyle: {
        color: '#6ba1bb',
        fontSize: 12
      },
      formatter: function (value) {
        // debugger
        var ret = '' // 拼接加\n返回的类目项
        var maxLength = 5 // 每项显示文字个数
        var valLength = value.length // X轴类目项的文字个数
        var rowN = Math.ceil(valLength / maxLength) // 类目项需要换行的行数
        if (rowN > 1) // 如果类目项的文字大于3,
        {
          for (var i = 0; i < rowN; i++) {
            var temp = '' // 每次截取的字符串
            var start = i * maxLength // 开始截取的位置
            var end = start + maxLength // 结束截取的位置
            // 这里也可以加一个是否是最后一行的判断，但是不加也没有影响，那就不加吧
            temp = value.substring(start, end) + '\n'
            ret += temp // 凭借最终的字符串
          }
          return ret
        } else {
          return value
        }
      }
    },
    axisLine: {
      lineStyle: {
        color: '#0a2b52',
        width: 0.5 // 这里是为了突出显示加上的
      }
    }
  }],
  yAxis: [{
    type: 'value',
    axisLine: {
      onZero: false,
      lineStyle: {
        color: '#0a2b52',
        width: 1 // 这里是为了突出显示加上的
      }
    },

    axisLabel: {
      formatter: function (val) {
        return val + ''
      },
      show: true,
      textStyle: {
        color: '#6ba1bb' // 字体颜色
      }
    },
    splitLine: {
      show: true,
      lineStyle: {
        type: 'dashed',
        color: '#00e4ff',
        opacity: 0.2
      }

    }
  }],
  series: [
    {
      name: '未戴白色厨帽',
      type: 'line',
      smooth: true,
      symbol: 'none', // 去掉折线点
      stack: 100,
      itemStyle: {
        normal: { // 颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
            offset: 0,
            color: '#b0f2e8' // 0% 处的颜色
          }, {
            offset: 0.5,
            color: '#b0f2e8' // 100% 处的颜色
          }, {
            offset: 1,
            color: '#38e2c8' // 100% 处的颜色
          }]), // 背景渐变色
          lineStyle: { // 系列级个性化折线样式
            width: 0.5,
            type: 'solid',
            color: '#18e4c5'
          }
        },
        emphasis: {
          color: '#18e4c5',
          lineStyle: { // 系列级个性化折线样式
            width: 0.5,
            type: 'dotted',
            color: '#18e4c5' // 折线的颜色
          }
        }
      }, // 线条样式
      symbolSize: 5, // 折线点的大小
      areaStyle: {
        normal: {}
      },
      data: dataIPS
    },

    {
      name: '未戴白色口罩',
      type: 'line',
      smooth: true,
      symbol: 'none', // 去掉折线点
      stack: 100,
      itemStyle: {
        normal: { // 颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
            offset: 0,
            color: '#64CAFA' // 0% 处的颜色
          }, {
            offset: 0.5,
            color: '#64CAFA' // 100% 处的颜色
          }, {
            offset: 1,
            color: '#0078D7' // 100% 处的颜色
          }]), // 背景渐变色
          lineStyle: { // 系列级个性化折线样式
            width: 0.5,
            type: 'solid',
            color: '#0078D7'
          }
        },
        emphasis: {
          color: '#02675f',
          lineStyle: { // 系列级个性化折线样式
            width: 0.5,
            type: 'dotted',
            color: '#02675f' // 折线的颜色
          }
        }
      }, // 线条样式
      symbolSize: 5, // 折线点的大小
      areaStyle: {
        normal: {}
      },
      data: dataIPS2
    },
    {
      name: '未穿白色厨师服',
      type: 'line',
      smooth: true,
      symbol: 'none', // 去掉折线点
      stack: 100,
      itemStyle: {
        normal: { // 颜色渐变函数 前四个参数分别表示四个位置依次为左、下、右、上
          color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [{
            offset: 0,
            color: '#bdb4c2' // 0% 处的颜色
          }, {
            offset: 0.5,
            color: '#7b5e8a' // 100% 处的颜色
          }, {
            offset: 1,
            color: '#6b4483' // 100% 处的颜色
          }]), // 背景渐变色
          lineStyle: { // 系列级个性化折线样式
            width: 0.5,
            type: 'solid',
            color: '#f48eba'
          }
        },
        emphasis: {
          color: '#f48eba',
          lineStyle: { // 系列级个性化折线样式
            width: 0.5,
            type: 'dotted',
            color: '#f48eba' // 折线的颜色
          }
        }
      }, // 线条样式
      symbolSize: 5, // 折线点的大小
      areaStyle: {
        normal: {}
      },
      data: dataIPS3
    }

  ]
}
export default {
  name: 'cook',
  props: {
    cookInfo: Object
  },
  data () {
    return {
      option: {}
    }
  },
  mounted () {
    this.initChart()
  },
  components: {
    block
  },
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(options)
    },
    setChartData () {
      const { hatMap, maskMap, clothesMap } = this.degree
      let newSeries = []
      options.series.map(item => {
        if (item.name === '未戴白色厨帽') {
          if (hatMap) {
            item.data = [hatMap.twentyFour || 0, hatMap.four || 0, hatMap.sixteen || 0, hatMap.twelve || 0, hatMap.eight || 0, hatMap.twenty || 0]
          }
        } else if (item.name === '未穿白色厨师服') {
          if (clothesMap) {
            item.data = [clothesMap.twentyFour || 0, clothesMap.four || 0, clothesMap.sixteen || 0, clothesMap.twelve || 0, clothesMap.eight || 0, clothesMap.twenty || 0]
          }
        } else {
          if (maskMap) {
            item.data = [maskMap.twentyFour || 0, maskMap.four || 0, maskMap.sixteen || 0, maskMap.twelve || 0, maskMap.eight || 0, maskMap.twenty || 0]
          }
        }
      })

      this.chart.setOption(options)
    }
  },
  watch: {
    degree: {
      handler (newValue, oldValue) {
        console.log('data changed....')
        this.setChartData()
      },
      deep: true
    }
  }

}
</script>

<style lang="less">
.chart-box{
  width: 100%;
  height:100%
}
</style>
