<template>
  <div clas="main">
    <dv-scroll-board style="width: 1230px;height: 466px;margin-left:98px;margin-right:4px;margin-top:50px"  :config="config" />
  </div>
</template>

<script>
export default {
  name: 'RealTimeAccess',
  props: {
    realTimeOrgJoinInfo: Array
  },
  data () {
    return {
      config: {
        header: ['餐饮单位名称', '安装数量', '在线数量', '接入时间'],
        data: [],
        carousel: 'page',
        align: ['center'],
        rowNum: 5,
        columnWidth: [600, 300, 300, 400],
        headerBGC: 'rgba(66,159,227,0.12)',
        headerHeight: 88,
        oddRowBGC: 'rgba(66,159,227,0.12)',
        evenRowBGC: 'rgba(10, 29, 50, 0.8)'
      }
    }
  },
  computed: {
    realTimeOrgJoinInfoLen () {
      console.log(this.realTimeOrgJoinInfo.length)
      return this.realTimeOrgJoinInfo.length
    }
  },

  components: {
  },
  watch: {
    realTimeOrgJoinInfoLen (newV, oldV) {
      if (newV) {
        let data = []
        this.realTimeOrgJoinInfo.map(item => {
          data.push(
            [item.orgName || '', item.total || '', item.onlineTotal || '', item.createTime || '']
          )
        })
        this.config = {
          header: ['餐饮单位名称', '安装数量', '在线数量', '接入时间'],
          data: data,
          carousel: 'page',
          align: ['center'],
          rowNum: 5,
          headerBGC: 'rgba(66,159,227,0.12)',
          headerHeight: 88,
          oddRowBGC: 'rgba(66,159,227,0.12)',
          evenRowBGC: 'rgba(10, 29, 50, 0.8)'
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.main{
  box-sizing: border-box;
  height: 100%;
  overflow: hidden;
  display: flex;
  align-items:center;
  align-content: center;
  justify-items: center;
  justify-items: center;
  text-align: center;

}
</style>
