<template>
  <div id="data-view">
    <div class="main-content">
        <div class="block-left-right-content">
          <!-- <ranking-board /> -->
          <div class="block-top-bottom-content">
            <div class="block-top-content">
              <left-container
              :cameraInfo='camera'
              :regionStatistics='regionStatistics'
              :degree='degree'
              :cabinet="cabinet"
              :cookInfo="cookInfo"
              />
              <center-container :totalJoinOrg='totalJoinOrg' :realTimeOrgJoinInfo='realTimeOrgJoinInfo' :regionStatistics='regionStatistics'/>
              <right-container :cameraTypeInfo='cameraTypeInfo' :cameraInfo='camera' :realTimeOrgJoinInfo='realTimeOrgJoinInfo'
              :companyRatio="companyRatio"/>
            </div>
          </div>
        </div>
      </div>
  </div>
</template>

<script>
import topHeader from './topHeader'
import digitalFlop from './digitalFlop'
import rankingBoard from './rankingBoard'
import leftContainer from '../custom/leftContainer'
import rightContainer from '../custom/rightContainer'
import centerContainer from '../custom/centerContainer'
import cards from './cards'

import { getRealTimeTotald, getCookWarn, getDegree, getCabinet,
  getTotalOrgStatistics, getRegionStatistics, getRealTimeOrgJoin,
  getCategoryJoinTotal, getCompanyRatio
} from '../../service/service'
const success = '0000'
export default {
  name: 'DataView',
  components: {
    topHeader,
    digitalFlop,
    rankingBoard,
    leftContainer,
    centerContainer,
    rightContainer,
    cards
  },
  data () {
    return {
      camera: {
        offline: 0,
        online: 0,
        onlineRatio: '100',
        total: 0
      },
      regionStatistics: [], // 地区接入数量
      realTimeOrgJoinInfo: [], // 实时接入信息
      totalJoinOrg: {// 总接入数量
        todayTotal: 0,
        previousTotal: 0
      },
      cameraTypeInfo: {
        categoryTotal: [],
        lastWeekCameraTotal: 0,
        lastWeekOrgTotal: 0
      },
      degree: {
        humidityMap: {
          twentyFour: 4,
          four: 0,
          sixteen: 0,
          twelve: 0,
          eight: 0,
          twenty: 0
        },
        temperatureMap: {
          twentyFour: 0,
          four: 0,
          sixteen: 0,
          twelve: 0,
          eight: 0,
          twenty: 0
        },
        certificateMap: {
          twentyFour: 0,
          four: 0,
          sixteen: 0,
          twelve: 0,
          eight: 0,
          twenty: 0
        }
      },
      cabinet: {}, // 消毒柜、留样柜、紫外灯
      cookInfo: {}, // 消毒柜、留样柜、紫外灯
      companyRatio: {
        companyRatio: {},
        companyTotal: {}
      }

    }
  },
  mounted () {
    this.timer = setInterval(this.init, 1000 * 120)
  },
  created () {
    this.init()
  },
  beforeDestroy () {
    clearInterval(this.timer)
  },
  methods: {
    init () {
      console.log('index page,query data.....')
      this.getRealTimeTotald()
      this.getCookWarn()
      this.getDegree()
      this.getCabinet()
      this.getTotalOrgStatistics()
      this.getRegionStatistics()
      this.realTimeOrgJoinFun()
      this.getCategoryJoinTotal()
      this.getCompanyRatioFun()
    },
    // 实时摄像头统计
    getRealTimeTotald () {
      getRealTimeTotald().then(res => {
        const { code, data } = res
        if (code === success) {
          this.camera = {
            offline: data.offline || 0,
            online: data.online || 0,
            onlineRatio: data.onlineRatio || '100',
            total: data.total || 0
          }
        }
      })
    },
    getCookWarn () {
      getCookWarn().then(res => {
        const { code, data } = res
        if (code === success) {
          this.cookInfo = data
        }
      })
    },
    getDegree () {
      getDegree().then(res => {
        const { code, data } = res
        if (code === success) {
          this.degree = data
        }
      })
    },
    getCabinet () {
      getCabinet().then(res => {
        const { code, data } = res
        if (code === success) {
          this.cabinet = data
        }
      })
    },
    // 共接入单位统计
    getTotalOrgStatistics () {
      getTotalOrgStatistics().then(res => {
        const { code, data } = res
        if (code === success) {
          this.totalJoinOrg = {
            todayTotal: data.todayTotal || 0,
            previousTotal: data.previousTotal || 0
          }
        }
      })
    },
    // 各地市接入单位数量统计排行
    getRegionStatistics () {
      getRegionStatistics().then(res => {
        const { code, data } = res
        if (code === success) {
          this.regionStatistics = data
        }
      })
    },
    // 实时单位接入统计（按单位的create_time来计算）
    realTimeOrgJoinFun () {
      getRealTimeOrgJoin().then(res => {
        const { code, data } = res
        if (code === success) {
          this.realTimeOrgJoinInfo = data && data.length ? data : []
        }
      })
    },
    // 摄像头分类接入统计
    getCategoryJoinTotal () {
      getCategoryJoinTotal().then(res => {
        const { code, data } = res
        if (code === success) {
          this.cameraTypeInfo = {
            categoryTotal: data.categoryTotal || [],
            lastWeekCameraTotal: data.lastWeekCameraTotal | 0,
            lastWeekOrgTotal: data.lastWeekOrgTotal | 0
          }
        }
      })
    },
    getCompanyRatioFun () {
      getCompanyRatio().then(res => {
        const { code, data } = res
        if (code === success) {
          this.companyRatio = data
        }
      })
    }
  }
}
</script>

<style lang="less">
#data-view {
  width: 100%;
  height: 100%;
  background-color: #0f2554;
  color: #fff;
  background-image: url('../../assets/img/bg.png');
  background-size: 100% 100%;

  #dv-full-screen-container {
    background-image: url('./img/bg.png');
    background-size: 100% 100%;
    box-shadow: 0 0 3px blue;
    display: flex;
    flex-direction: column;
  }

  .main-content {
    padding-top: 274px;
    padding-left:104px;
    flex: 1;
    display: flex;
    flex-direction: column;
  }

  .block-left-right-content {
    flex: 1;
    display: flex;
    margin-top: 20px;
  }

  .block-top-bottom-content {
    flex: 1;
    display: flex;
    flex-direction: column;
    box-sizing: border-box;
    padding-left: 20px;
    padding-right: 20px;
  }

  .block-top-content {
    height: 100%;
    display: flex;
    flex-grow: 0;
    box-sizing: border-box;
    padding-bottom: 20px;
    flex-direction: row
  }
}
</style>
