        }
      }
    }
  }
  .bottom{
    width: 650px;
    height: 738px;
    position:absolute;
    left: 2718px;
    top:1900px;
    z-index: 9999;
    .time{
      width: 100%;
      height: 71px;
      font-family: Helvetica;
      font-size: 98px;
      font-weight: normal;
      font-stretch: normal;
      line-height: 48px;
      letter-spacing: 8px;
	    color: #0bdef3;
      }
      .date{
        width: 100%;
