      option: {}
    }
  },
  mounted () {
    this.initChart()
  },
  components: {
    block
  },
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(option)

      // this.leftChart = echarts.init(this.$refs.leftChartRef)
      // this.leftChart.setOption(leftChartOption)
    },
    setChartData () {
      const { companyTotal, companyRatio } = this.companyRatio
      if (companyTotal) {
        let newData = []
        companyTotal.map(item => {
          if (item.controlUnitName === '学校食堂') {
            newData.push({
              value: item.count,
              name: '学校食堂',
              itemStyle: {
                normal: {
                  color: '#18e4c5'
                }
              }
            })
          } else if (item.controlUnitName === '社会餐饮') {
            newData.push({
              value: item.count,
              name: '社会餐饮'
            })
          } else if (item.controlUnitName === '职工食堂') {
            newData.push({
              value: item.count,
              name: '职工食堂'
            })
          } else if (item.controlUnitName === '农村聚餐') {
            newData.push({
              value: item.count,
              name: '农村聚餐'
            })
          } else if (item.controlUnitName === '阳光车间') {
            newData.push({
              value: item.count,
              name: '阳光车间'
            })
          }
        })
        option.series[0].data = newData
      }
      if (companyRatio) {
        const xxst = companyRatio['学校食堂'] || 0
        const shcy = companyRatio['社会餐饮'] || 0
        const ncst = companyRatio['农村聚餐'] || 0
        const ygcj = companyRatio['阳光车间'] || 0
        const zgst = companyRatio['职工食堂'] || 0
        option.series.map(item => {
          if (item.name === '1') {
            item.data = this.getData1(xxst)
          } else if (item.name === '1-1') {
            item.data = this.getData1(xxst)
          } else if (item.name === '2' || item.name === '2-1') {
            item.data = this.getData1(shcy)
          } else if (item.name === '3' || item.name === '3-1') {
            item.data = this.getData1(ncst)
          } else if (item.name === '4' || item.name === '4-1') {
            item.data = this.getData1(ygcj)
          } else if (item.name === '5' || item.name === '5-1') {
            item.data = this.getData1(zgst)
          }
        })
      }
      this.chart.setOption(option)
    },
    getData1 (value) {
      const v = parseInt(value)
      const data = [{
        value: v,
        itemStyle: {
          normal: {
            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
              {
                offset: 0,
                color: '#9f3edd'
              },
              {
                offset: 1,
                color: '#4897f6'
              }
            ])
          }
        },
        label: dataStyle
      },
      {
        value: 100 - v,
        itemStyle: placeHolderStyle
      }
      ]
      return data
    }
  },
  watch: {
    companyRatio: {
      handler (newValue, oldValue) {
        console.log('data changed....')
        this.setChartData()
      },
      deep: true
    }
  }
}
