<template>
  <block title="">
      <div class="chart-box" ref="chartRef">

      </div>
    </block>
</template>

<script>
import block from '../../../../component/block'
import echarts from 'echarts'
const options = {
  tooltip: {
    trigger: 'axis'
  },
  grid: {
    x: 46,
    y: 30,
    x2: 30,
    y2: 20,
    borderWidth: 0
  },

  calculable: false,
  legend: {
    data: ['温度', '湿度', '持证上岗'],
    textStyle: {
      color: '#e9ebee'
    },
    right: '5%'
  },
  xAxis: [
    {
      type: 'category',
      data: [ '04', '08', '12', '16', '20', '24'],
      splitLine: {
        show: false
      },
      axisLabel: {
        show: true,
        textStyle: {
          color: '#a4a7ab',
          align: 'center'
        }
      }

    }
  ],
  yAxis: [
    {
      type: 'value',

      name: '订单量',
      axisLabel: {
        formatter: '{value} ',
        textStyle: {
          color: '#a4a7ab',
          align: 'right'
        }
      },
      splitLine: {
        show: true,
        lineStyle: {
          type: 'dashed',
          color: '#00e4ff',
          opacity: 0.2
        }
      }
    },
    {
      type: 'value',
      name: '温度',
      axisLabel: {
        formatter: '{value} °C',
        textStyle: {
          color: '#a4a7ab',
          align: 'right'
        }
      },
      splitLine: {
        show: false
      }
    }

  ],
  series: [
    {
      name: '温度',
      type: 'bar',
      data: [],
      itemStyle: {
        normal: {
          color: '#18e4c5'
        }
      }
    },
    {
      name: '湿度',
      type: 'bar',
      data: [],
      itemStyle: {
        normal: {
          color: '#3e93f5'
        }
      }
    },
    {
      name: '持证上岗',
      type: 'line',
      data: [],
      itemStyle: {
        normal: {
          color: '#ffb017'
        }
      }
    }
  ]
}

export default {
  name: 'TemperatureHumidity',
  props: {
    degree: Object
  },
  data () {
    return {
      option: {}
    }
  },
  mounted () {
    this.initChart()
  },
  components: {
    block
  },
  methods: {
    initChart () {
      console.log(this.degree)
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(options)
    },
    setChartData () {
      const { humidityMap, temperatureMap, certificateMap } = this.degree
      let newSeries = []
      if (humidityMap) {
        newSeries.push({
          name: '温度',
          type: 'bar',
          data: [humidityMap.twentyFour || 0, humidityMap.four || 0, humidityMap.sixteen || 0, humidityMap.twelve || 0, humidityMap.eight || 0, humidityMap.twenty || 0],
          itemStyle: {
            normal: {
              color: '#18e4c5'
            }
          }
        })
      }
      if (temperatureMap) {
        newSeries.push({
          name: '湿度',
          type: 'bar',
          data: [humidityMap.twentyFour || 0, humidityMap.four || 0, humidityMap.sixteen || 0, humidityMap.twelve || 0, humidityMap.eight || 0, humidityMap.twenty || 0],
          itemStyle: {
            normal: {
              color: '#3e93f5'
            }
          }
        })
      }
      if (certificateMap) {
        newSeries.push({
          name: '持证上岗',
          type: 'line',
          data: [certificateMap.twentyFour || 0, certificateMap.four || 0, certificateMap.sixteen || 0, certificateMap.twelve || 0, certificateMap.eight || 0, certificateMap.twenty || 0],
          itemStyle: {
            normal: {
              color: '#ffb017'
            }
          }
        })
      }
      options.series = newSeries
      this.chart.setOption(options)
    }
  },
  watch: {
    degree: {
      handler (newValue, oldValue) {
        console.log('data changed....')
        this.setChartData()
      },
      deep: true
    }
  }

}
</script>

<style lang="less">
.chart-box{
  width: 100%;
  height:100%
}
</style>
