<template>
  <block title="">
      <div class="chart-box" ref="chartRef">

      </div>
    </block>
</template>

<script>
import block from '../../../../component/block'
import echarts from 'echarts'
const options = {
  tooltip: {
    trigger: 'axis'
  },
  grid: {
    x: 46,
    y: 30,
    x2: 30,
    y2: 20,
    borderWidth: 0
  },

  calculable: false,
  legend: {
    data: ['消毒柜', '留样柜', '紫外灯'],
    textStyle: {
      color: '#e9ebee'
    },
    right: '5%'
  },
  xAxis: [
    {
      type: 'category',
      data: [ '04', '08', '12', '16', '20', '24'],
      splitLine: {
        show: false
      },
      axisLabel: {
        show: true,
        textStyle: {
          color: '#a4a7ab',
          align: 'center'
        }
      }

    }
  ],
  yAxis: [
    {
      type: 'value',

      axisLabel: {
        formatter: '{value} ',
        textStyle: {
          color: '#a4a7ab',
          align: 'right'
        }
      },
      splitLine: {
        show: true,
        lineStyle: {
          type: 'dashed',
          color: '#18e4c5',
          opacity: 0.1
        }
      }
    },
    {
      type: 'value',
      name: '温度',
      axisLabel: {
        formatter: '{value} °C',
        textStyle: {
          color: '#a4a7ab',
          align: 'right'
        }
      },
      splitLine: {
        show: false
      }
    }

  ],
  series: [
    {
      name: '消毒柜',
      type: 'bar',
      data: [],
      itemStyle: {
        normal: {
          color: '#18e4c5'
        }
      }
    },
    {
      name: '留样柜',
      type: 'bar',
      data: [],
      itemStyle: {
        normal: {
          color: '#3e93f5'
        }
      }
    },
    {
      name: '紫外灯',
      type: 'bar',
      data: [],
      itemStyle: {
        normal: {
          color: '#ffb017'
        }
      }
    }
  ]
}

export default {
  name: 'sterilizer',
  props: {
    cabinet: Object
  },
  data () {
    return {
      option: {}
    }
  },
  mounted () {
    this.initChart()
  },
  components: {
    block
  },
  methods: {
    initChart () {
      this.chart = echarts.init(this.$refs.chartRef)
      // 把配置和数据放这里
      this.chart.setOption(options)
    },
    setChartData () {
      const { sampleCabinetMap, disinfectionCabinetMap, lampMap } = this.degree
      let newSeries = []
      if (sampleCabinetMap) {
        newSeries.push({
          name: '消毒柜',
          type: 'bar',
          data: [disinfectionCabinetMap.twentyFour || 0, disinfectionCabinetMap.four || 0, disinfectionCabinetMap.sixteen || 0, disinfectionCabinetMap.twelve || 0, disinfectionCabinetMap.eight || 0, disinfectionCabinetMap.twenty || 0],
          itemStyle: {
            normal: {
              color: '#18e4c5'
            }
          }
        })
      }
      if (disinfectionCabinetMap) {
        newSeries.push({
          name: '留样柜',
          type: 'bar',
          data: [sampleCabinetMap.twentyFour || 0, sampleCabinetMap.four || 0, sampleCabinetMap.sixteen || 0, sampleCabinetMap.twelve || 0, sampleCabinetMap.eight || 0, sampleCabinetMap.twenty || 0],
          itemStyle: {
            normal: {
              color: '#3e93f5'
            }
          }
        })
      }
      if (lampMap) {
        newSeries.push({
          name: '紫外灯',
          type: 'line',
          data: [lampMap.twentyFour || 0, lampMap.four || 0, lampMap.sixteen || 0, lampMap.twelve || 0, lampMap.eight || 0, lampMap.twenty || 0],
          itemStyle: {
            normal: {
              color: '#ffb017'
            }
          }
        })
      }
      options.series = newSeries
      this.chart.setOption(options)
    }
  },
  watch: {
    degree: {
      handler (newValue, oldValue) {
        console.log('data changed....')
        this.setChartData()
      },
      deep: true
    }
  }

}
</script>

<style lang="less">
.chart-box{
  width: 100%;
  height:100%
}
</style>
